"use client";

import { Download, Upload } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ImportExportDialog } from "./import-export-dialog";

interface ProjectActionsProps {
  projectId?: string; // 用于导出
  onImportComplete?: () => void;
}

export function ProjectActions({ projectId, onImportComplete }: ProjectActionsProps) {
  return (
    <div className="flex items-center gap-2">
      {/* 导入按钮 */}
      <ImportExportDialog mode="import" onImportComplete={onImportComplete}>
        <Button variant="outline" size="sm">
          <Upload className="h-4 w-4 mr-2" />
          Import
        </Button>
      </ImportExportDialog>

      {/* 导出按钮 */}
      {projectId && (
        <ImportExportDialog mode="export" projectId={projectId}>
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
        </ImportExportDialog>
      )}

      {/* 或者使用下拉菜单 */}
      {projectId && (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="outline" size="sm">
              <Download className="h-4 w-4 mr-2" />
              Export
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <ImportExportDialog mode="export" projectId={projectId}>
              <DropdownMenuItem onSelect={(e) => e.preventDefault()}>
                <Download className="h-4 w-4 mr-2" />
                Export Project
              </DropdownMenuItem>
            </ImportExportDialog>
            <DropdownMenuSeparator />
            <ImportExportDialog mode="import">
              <DropdownMenuItem onSelect={(e) => e.preventDefault()}>
                <Upload className="h-4 w-4 mr-2" />
                Import Project
              </DropdownMenuItem>
            </ImportExportDialog>
          </DropdownMenuContent>
        </DropdownMenu>
      )}
    </div>
  );
}
